body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-width:1000px; 
  height:auto; 
  overflow-x: hidden;

}

.root {
  overflow-y: hidden;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  min-width:1000px; 
  height:auto; 
}
